<template>
    <div class="edit-info">
        <h2 class="title">
            {{$route.name == '修改客服消息' ? '修改客服消息' : '新建客服消息'}}
        </h2>
        <Form :model="form" class="form-group" label-position="right" :label-width="100">
            <FormItem label="类型" prop="type">
                <RadioGroup v-model="type" type="button" @on-change="changeType">
                    <Radio :label="1">文本</Radio>
                    <Radio :label="0">图文</Radio>
                </RadioGroup>
            </FormItem>
            <Form :model="subForm" label-position="right" :label-width="100" v-if="type == 0">
                <FormItem label="图片" prop="picurl">
                    <Upload action="http://test.oorggt.top/reply/upload" :show-upload-list="false" name="imageFile" :on-success="handleSuccess">
                        <Button icon="ios-cloud-upload-outline">点击上传</Button>
                        <br />
                        <img v-if="imgUrl" :src="imgUrl" alt="" width="200" style="margin-top:10px;">
                    </Upload>
                </FormItem>
                <FormItem label="标题" prop="title">
                    <Input size="large" type="text" v-model="subForm.title" placeholder="请输入标题"></Input>
                </FormItem>
                <FormItem label="链接" prop="url">
                    <Input size="large" type="text" v-model="subForm.url" placeholder="请输入链接"></Input>
                </FormItem>
                <FormItem label="详细信息" prop="description">
                    <Input size="large" type="textarea" v-model="subForm.description" placeholder="请输入详细信息"></Input>
                </FormItem>
                <FormItem>
                    <Button class="edit-btn" size="large" type="success" @click="addOne">添加</Button>
                    <Button class="edit-btn" size="large" type="primary" @click="saveOne">保存</Button>
                </FormItem>
                <FormItem label="图文消息">
                    <Table class="edit-message-table" stripe :columns="editHeader" :data="editData"></Table>
                </FormItem>
            </Form>
            <FormItem v-else-if="type == 1" label="详细信息" prop="description">
                <Input size="large" type="textarea" v-model="description" placeholder="请输入详细信息"></Input>
            </FormItem>
            <FormItem label="人工/延时/定时" prop="channel">
                <RadioGroup v-model="channel" type="button">
                    <Radio :label="0">人工</Radio>
                    <Radio :label="1">延时</Radio>
                    <Radio :label="2">定时</Radio>
                </RadioGroup>
            </FormItem>
            <FormItem v-if="channel == 1" label="延时时间(分钟)" prop="delay">
                <Input size="large" type="text" v-model="delay" placeholder="请输入延时时间(分钟)" style="width: 180px;"></Input>
            </FormItem>
            <FormItem v-if="channel == 2" label="定时时间" prop="timing_time">
                <DatePicker type="datetime" placeholder="选择日期时间" style="width: 200px" v-model="timing_time"></DatePicker>
            </FormItem>
            <FormItem label="选择标签" prop="tagName">
                <Tag v-if="tagName" :name="tagName" closable @on-close="handleClose">{{tagName}}</Tag>
            </FormItem>
            <FormItem label="标签选项">
                <Tag v-for="(item, index) in tagList" :name="item.name" @click.native="select(item)">{{item.name}}</Tag>
            </FormItem>
            <FormItem label="是否接管" prop="timing_time">
                <i-switch v-model="flag" @on-change="changeFlag"></i-switch>
            </FormItem>
            <FormItem label="选择公号" prop="codes1" v-if="flag">
                <CheckboxGroup v-model="codes1">
                    <Checkbox v-for="(item, index) in gonghaoList1" :label="item.code" name="codes1">{{item.name}}</Checkbox>
                </CheckboxGroup>
            </FormItem>
            <FormItem label="选择公号" prop="codes2" v-else>
                <CheckboxGroup v-model="codes2">
                    <Checkbox v-for="(item, index) in gonghaoList2" :label="item.code" name="codes1">{{item.name}}</Checkbox>
                </CheckboxGroup>
            </FormItem>
            <FormItem>
                <Button class="edit-btn" size="large" type="success" @click="onSave">保存</Button>
                <Button class="edit-btn" size="large" type="warning" @click="cancel">取消</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
            form: {},
            type: 1,
            subForm: {},
            editData: [],
            codes: [],
            codes1: [],
            codes2: [],
            contents: [],
            task: false,
            delay: "",
            description: "",
            picMessageSwitch: false,
            imgUrl: "",
            gonghaoList1: [],
            gonghaoList2: [],
            flag: true,
            obj: {},
            contentData: {},
            channel: 0,
            timing_time: new Date(),
            tagName: "",
            tagList: [],
            tagId: "",
            editHeader: [
                    {
                        key: 'title',
                        title: '标题',
                        align: 'center'
                    }, {
                        key: 'picurl',
                        title: '图片',
                        align: 'center',
                        render: (h, params) => {
                            return h('img', {
                                attrs: {
                                    src: params.row.picurl,
                                    style: 'width:100%;padding:10px;'
                                }
                            })
                        }
                    }, {
                        key: 'url',
                        title: '链接',
                        align: 'center'
                    }, {
                        key: 'description',
                        title: '详细信息',
                        align: 'description',
                        width: 100
                    }, {
                        title: 'Action',
                        align: 'center',
                        render: (h, params) => {
                            let that = this;
                            return h('div', [
                                h('Button', {
                                    style: {
                                        marginRight: '10px'
                                    },
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.subForm = params.row;
                                            that.subForm.index = params.index;
                                            that.imgUrl = params.row.picurl;
                                        }
                                    }
                                }, '修改'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            if (that.$route.name == 'messageUpdate' && that.editData.length <= 1) {
                                                that.$Message.info("列表中最少展示一条数据");
                                            } else {
                                                that.editData.splice(params.index, 1);
                                                that.$Message.info("删除成功");
                                            }
                                        }
                                    }
                                }, '删除'),
                            ])
                        }
                    }
                ]
            };
        },
        mounted() {
            this.get_codes();
            this.showTagList();
            if(this.$route.name == '修改客服消息') {
                this.obj = this.$route.params
                this.editData = this.obj.contents;
                this.type = this.obj.type;
                this.description = this.obj.contents[0].description;
                this.subForm = this.editData[0]
                console.log(this.subForm)
                this.imgUrl = this.subForm.picurl
                this.delay = this.obj.delay;
                this.channel = this.obj.is_timing ? 2 : this.obj.task ? 1 : 0;
                this.timing_time = this.obj.timing_time;
                this.flag = this.obj.take_over;
                this.tagId = this.obj.tagId;
                if (this.flag) {
                    this.codes1 = this.obj.codes;
                } else {
                    this.codes2 = this.obj.codes;
                }
                if(this.tagId) {
                    this.showList();
                }
            }
        },
        methods: {
            handleClose() {
                this.tagName = "";
            },
            showTagList() {
                this.$axios.get('/tag')
                .then(res => {
                    this.tagList = res.data.data;
                });
            },
            select(item) {
                this.tagId = item._id;
                this.tagName = item.name;
            },
            onSave() {
                if (this.type == 1) {
                    this.contentData.description = this.description;
                    this.contents.push(this.contentData);
                } else {
                    this.contents = this.editData;
                }
                if (this.flag == true) {
                    this.codes = this.codes1;
                } else {
                    this.codes = this.codes2;
                }
                if(this.$route.name == "添加客服消息") {
                    this.$axios.post('/message/create', {
                        contents: this.contents,
                        type: this.type,
                        task: this.channel == "1" ? true : false,
                        is_timing: this.channel == "2" ? true : false,
                        delay: this.delay,
                        timing_time: this.timing_time == "" ? "" : this.timing_time,
                        codes: this.codes,
                        img: this.contents[0].picurl,
                        take_over: this.flag,
                        tagId: this.tagId
                    }).then(res => {
                        if (res.data.success) {
                            this.reset()
                            window.location.href = "/msg_view/manage";
                            this.$Message.info(res.data.success);
                        } else {
                            this.$Message.info(res.data.err);
                        }
                    });
                } else if(this.$route.name == "修改客服消息") {
                    this.$axios.post('/message/update', {
                        id: this.obj._id,
                        contents: this.contents,
                        type: this.type,
                        task: this.channel == "1" ? true : false,
                        is_timing: this.channel == "2" ? true : false,
                        delay: this.delay,
                        timing_time: this.timing_time == "" ? "" : this.timing_time,
                        codes: this.codes,
                        img: this.contents[0].picurl,
                        take_over: this.flag,
                        tagId: this.tagName == "" ? "" : this.tagId
                    }).then(res => {
                        if (res.data.success) {
                            this.reset()
                            window.location.href = "/msg_view/manage";
                            this.$Message.info(res.data.success);
                        } else {
                            this.$Message.info(res.data.err);
                        }
                    });
                }
                
            },
            addOne() {
                this.subForm.picurl = this.imgUrl;
                this.editData.push(this.subForm);
                this.subForm = {};
                this.imgUrl = "";
            },
            saveOne() {
                this.editData.splice(this.subForm.index, 1, this.subForm);
                this.subForm = {};
                this.imgUrl = "";
            },
            changeType() {
                this.reset()
            },
            changeFlag() {
                this.codes1 = this.codes2 = [];
            },
            handleSuccess(res, file) {
                this.imgUrl = "http://test.oorggt.top/uploads/" + res.filename;
            },
            showList() {
                this.$axios.get('/tag/get_name', {params: {tagId: this.tagId}})
                .then(res => {
                    this.tagName = res.data.data.name;
                });
            },
            reset() {
                this.form = {};
                this.subForm = {}
                this.imgUrl = '';
                this.editData = []
            },
            cancel() {
                this.reset()
                this.$router.push({name: '客服消息'})
            },
            get_codes() {
                this.$axios.get('/message/get_code')
                .then(res => {
                    if (res.data.codes != "") {
                        let arr1 = [];
                        let arr2 = [];
                        for (let i = 0; i < res.data.codes.length; i++) {
                            let index = i;
                            if (res.data.codes[index].take_over == 1) {
                            arr1.push(res.data.codes[index]);
                            } else {
                            arr2.push(res.data.codes[index]);
                            }
                        }
                        this.gonghaoList1 = arr1;
                        this.gonghaoList2 = arr2;
                    }
                });
            }
        }
    };
</script>

<style scoped>
    .edit-info {
        width: 600px;
        text-align: left;
        margin-left: 100px;
    }
    .edit-btn {
        margin-right: 20px;
    }
    .edit-message-table {
        width: 150%;
    } 
    .title {
        margin-bottom: 30px;
        font-size: 30px;
        margin-left: 100px;
    }
</style>

